<template>
  <div v-loading="loading" element-loading-text="拼命加载中">
    <div class="handle-box">
      <div class="btn">
        <el-button type="primary" @click="addTest">添加考试</el-button>
      </div>
    </div>

    <el-dialog title="添加考试" :visible.sync="dialogTest">
      <div class="box">
        <el-form :model="form" label-width="80px">
          <el-form-item label="时间">
            <el-date-picker v-model="form.time" type="date" placeholder="选择日期" format="yyyy 年 MM 月 dd 日"
              value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
          <el-form-item label="标题">
            <el-input v-model="form.title" placeholder="请输入标题(例:期中考试)"></el-input>
          </el-form-item>
          <div class="submit">
            <el-form-item>
              <el-button type="primary" @click="newTest">提交</el-button>
            </el-form-item>
          </div>
        </el-form>
      </div>
    </el-dialog>

    <el-table :data="tableDate" border :header-cell-style="{background:'#f0f0f0'}" max-height="620">
      <el-table-column prop="id" label="ID"></el-table-column>
      <el-table-column prop="title" label="标题"></el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button type="primary" size="mini" @click="handleDetail(scope.$index, scope.row)">考试详情</el-button>
          <el-button type="danger" size="mini" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination @current-change="currentChange" :current-page.sync="current" :page-sizes="[10, 20, 30, 40, 50]"
        :page-size="size" layout="sizes, prev, pager, next, jumper" :total="total" @size-change="sizeChange">
      </el-pagination>
    </div>
    <!-- 查看考试详情 -->
    <el-dialog :visible.sync="dialogDetail" title="考试详情" :close-on-click-modal="false" width="80%">
      <el-table :data="testDetail" max-height="620" border :header-cell-style="{background:'#f0f0f0'}">
        <el-table-column prop="grade" label="年级"></el-table-column>
        <el-table-column prop="class" label="班级"></el-table-column>
        <el-table-column prop="number" label="学号"></el-table-column>
        <el-table-column prop="name" label="学生姓名"></el-table-column>
        <el-table-column prop="score" label="总分"></el-table-column>
        <el-table-column prop="class_ranking" label="班级排名"></el-table-column>
        <el-table-column prop="grade_ranking" label="年级排名"></el-table-column>
        <el-table-column prop="class_advance" label="班级进退步"></el-table-column>
        <el-table-column prop="grade_advance" label="年级进退步"></el-table-column>
        <el-table-column label="操作">
          <template slot-scope="scope">
            <el-button size="mini" type="primary" @click="handleExam(scope.$index,scope.row)">查看各科成绩</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="block">
        <el-pagination @current-change="testDetailCurrentChange" :current-page.sync="testDetailCurrent"
          :page-sizes="[10, 20, 30, 40, 50]" :page-size="testDetailSize" layout="sizes, prev, pager, next, jumper"
          :total="testDetailTotal" @size-change="testDetailSizeChange"></el-pagination>
      </div>
    </el-dialog>
    <!-- 各科成绩 -->
    <el-dialog :visible.sync="dialogExam" title="各科成绩" :close-on-click-modal="false" width="80%" align="center">
      <table border="1" width="1000px" class="table" :model="examList">
        <tr class="trHeight bg">
          <td width="150px" v-for="(item, index) in examList" :key="index">{{item.subject}}</td>
        </tr>
        <tr class="trHeight">
          <td v-for="(item, index1) in examList" :key="index1">{{item.score}}</td>
        </tr>
      </table>
    </el-dialog>
    <!-- 删除提示框 -->
    <el-dialog :visible.sync="dialogDel" title="删除资讯" width="20%" align="center" :close-on-click-modal="false">
      <div style="font-size: 20px; margin-bottom: 30px;">是否删除</div>
      <span>
        <el-button type="primary" @click="toDel">删除</el-button>
        <el-button type="danger" @click="dialogDel = false">取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  import API from "@/api/index.js";

  export default {
    name: "messagetype",
    data() {
      return {
        loading: true,
        dialogTest: false,
        id: "",
        dialogDel: false,
        dialogDetail: false, // 查看考试详情
        testDetail: [],
        testDetailCurrent: 1,
        testDetailSize: 10,
        testDetailTotal: 0,
        form: {
          title: "",
          time: "",
        },
        tableDate: [],
        current: 1,
        size: 10,
        total: 0,
        exam_id: "",
        dialogExam: false,
        examList: [],
      };
    },
    mounted() {
      this.getExams(this.current, this.size);
    },
    methods: {
      getExams(cur, list) {
        var self = this;
        API.exams(cur, list)
          .then((res) => {
            self.loading = false;
            self.tableDate = res.data;
            self.total = res.total;
          })
          .catch((err) => {
            self.loading = false;
          });
      },
      // 分页
      currentChange(val) {
        var self = this;
        self.current = val;
        self.loading = true;
        self.getExams(val, self.size);
      },
      sizeChange(val) {
        var self = this;
        self.size = val;
        self.loading = true;
        self.getExams(1, val);
        self.current = 1;
      },

      // 添加考试
      addTest() {
        var self = this;
        self.dialogTest = true;
        self.form = {
          title: "",
          time: "",
        };
      },
      newTest() {
        var self = this;
        self.form.title = self.form.time + " " + self.form.title;
        API.exam(self.form).then((res) => {
          self.$message.success("提交成功");
          self.dialogTest = false;
          self.getExams(self.current, self.size);
          self.form = {
            title: "",
            time: "",
          };
        });
      },

      // 操作
      handleDetail(index, row) {
        var self = this;
        self.testDetailCurrent = 1;
        self.testDetailSize = 10;
        self.dialogDetail = true;
        self.exam_id = row.id;
        self.getDetail(self.testDetailCurrent, self.testDetailSize);
      },
      // 考试详情
      getDetail(cur, list) {
        var self = this;
        API.examsDetail(cur, list, self.exam_id).then((res) => {
          self.$message.success("获取数据成功");
          self.testDetail = res.data;
          self.testDetailTotal = res.total;
        });
      },
      // 详情分页
      testDetailCurrentChange(val) {
        var self = this;
        self.testDetailCurrent = val;
        self.getDetail(val, self.testDetailSize);
      },
      testDetailSizeChange(val) {
        var self = this;
        self.testDetailSize = val;
        self.getDetail(1, val);
        self.testDetailCurrent = 1;
      },
      handleExam(index, row) {
        var self = this;
        self.dialogExam = true;
        self.examList = self.testDetail[index].scores;
      },

      handleDelete(index, row) {
        var self = this;
        self.dialogDel = true;
        self.id = row.id;
      },
      toDel() {
        var self = this;
        API.delExam(self.id).then((res) => {
          self.$message.success("删除成功");
          self.dialogDel = false;
          self.getExams(self.current, self.size);
        });
      },

    },
  };
</script>

<style scoped>
  .upload-btn {
    margin-top: 10px;
  }

  .pic-box {
    max-width: 100%;
    height: 200px;
  }

  .table .trHeight {
    height: 40px;
    text-align: center;
  }

  .bg {
    background-color: #ebeef5;
  }
</style>